import react, {Component} from 'react';
import {Container, Title} from'bloomer'

const logos ={
    backgroundColor: '#009688',
    height: '40px',
    padding: '10px'
}

export default class Login extends Component{

    render(){
        return(
            <div style={logos}>
            <Container>
                <Title>Please Login</Title>
                </Container></div>
        )
    }

}


